/*BODY*/
body {
  font-size: 20px;
  font-weight: 400;
  font-family: "Inter", sans-serif;
  line-height: 140%;
  color: #101010;
  margin: 0px;
}

/*SECTION*/
section {
  padding-top: 80px;
  padding-bottom: 80px;
}

.dark {
  background-color: #1E1E1E;
  color: #fff;
}

@media (max-width: 680px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}
/*CONTENER*/
.contener {
  max-width: 680px;
  margin: 0 auto;
  padding: 0px 16px;
}

.contener-img {
  max-width: 680px;
  margin: 0 auto;
}

/*LINK for white BG*/
.cs-link {
  color: #19A0FC;
  text-decoration: none;
}
.cs-link:hover {
  text-decoration: underline;
}

/*TITLES*/
.case-title {
  font-size: 28px;
  font-weight: 500;
  margin: 0px 0px 52px 0px;
}

@media (max-width: 680px) {
  .case-title {
    font-size: 20px;
    margin: 0px 0px 52px 0px;
  }
}
.case__wrapper80 {
  padding: 80px 0px;
}

.list__item {
  display: flex;
  gap: 30px;
  position: relative;
}
.list__item .akapit {
  display: block;
  margin-bottom: 8px;
}
.list__item .list__number {
  font-size: 16px;
  position: absolute;
  left: -30px;
}
@media (max-width: 820px) {
  .list__item .list__number {
    position: relative;
    left: 0px;
  }
}
@media (max-width: 680px) {
  .list__item .list__number {
    font-size: 14px;
  }
}
.list__item .list__text--bottom {
  color: #888;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 12px;
}

@media (max-width: 680px) {
  .list__item {
    gap: 16px;
  }
}
.header {
  width: 100%;
  padding: 40px 0px 80px 0px;
  position: sticky;
  top: 0;
  z-index: 1000;
  font-size: 16px;
}

.header__nav-wrapper {
  display: flex;
  gap: 12px;
}
.header__nav-wrapper .button__nav {
  background-color: rgba(255, 255, 255, 0.8);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  border-radius: 8px;
  border: 1.3px solid rgba(15, 27, 94, 0.1);
  font-weight: 500;
}
.header__nav-wrapper .button__nav--link {
  color: #101010;
  text-decoration: none;
  display: block;
  padding: 6px 12px;
}
.header__nav-wrapper .button__nav--dark {
  background-color: #1E1E1E;
  border: 1.3px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  font-weight: 400;
}
.header__nav-wrapper .button__nav--dark .button__nav--link {
  color: #fff;
}

.hero {
  padding-bottom: 80px;
}
.hero .hero__wrapper {
  padding-bottom: 80px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.hero .hero__wrapper .case-title {
  margin-bottom: 16px;
  line-height: 140%;
}

@media (max-width: 680px) {
  .contener {
    height: 90%;
  }
}
.toolbar {
  width: 100%;
  height: 19px;
  background: linear-gradient(#F4F4F4, #E7E7E7);
  position: relative;
}
.toolbar__dots {
  display: flex;
  gap: 4px;
  position: absolute;
  left: 8px;
  top: 6px;
}
.toolbar__dots .toolbar__dot {
  width: 8px;
  height: 8px;
  background-color: #CBCBCD;
  border-radius: 100px;
}
.toolbar__title {
  font-size: 9px;
  color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dark .toolbar {
  background: #343434;
}
.dark .toolbar__dot {
  background-color: #686866;
}
.dark .toolbar__title {
  color: rgba(255, 255, 255, 0.5);
}

.desktop {
  display: flex;
  flex-direction: column;
  gap: 0px;
  border-radius: 4px;
  overflow: hidden;
}
.desktop img {
  max-width: 680px;
  width: 100%;
}

.contact {
  padding: 80px 0px;
}
.contact .contact__links {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.contact .contact__links .cs-link {
  display: block;
  color: #19A0FC;
}
.contact .contact__links .cs-link::after {
  content: " " url(/img/arrow-up-right.svg);
}