body {
  color: #101010;
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 140%;
}

section {
  padding-top: 80px;
  padding-bottom: 80px;
}

.dark {
  color: #fff;
  background-color: #1e1e1e;
}

@media (width <= 680px) {
  body {
    font-size: 16px;
    line-height: 140%;
  }
}

.contener {
  max-width: 680px;
  margin: 0 auto;
  padding: 0 16px;
}

.contener-img {
  max-width: 680px;
  margin: 0 auto;
}

.cs-link {
  color: #19a0fc;
  text-decoration: none;
}

.cs-link:hover {
  text-decoration: underline;
}

.case-title {
  margin: 0 0 52px;
  font-size: 28px;
  font-weight: 500;
}

@media (width <= 680px) {
  .case-title {
    margin: 0 0 52px;
    font-size: 20px;
  }
}

.case__wrapper80 {
  padding: 80px 0;
}

.list__item {
  gap: 30px;
  display: flex;
  position: relative;
}

.list__item .akapit {
  margin-bottom: 8px;
  display: block;
}

.list__item .list__number {
  font-size: 16px;
  position: absolute;
  left: -30px;
}

@media (width <= 820px) {
  .list__item .list__number {
    position: relative;
    left: 0;
  }
}

@media (width <= 680px) {
  .list__item .list__number {
    font-size: 14px;
  }
}

.list__item .list__text--bottom {
  color: #888;
  flex-direction: column;
  gap: 8px;
  margin-top: 12px;
  display: flex;
}

@media (width <= 680px) {
  .list__item {
    gap: 16px;
  }
}

.header {
  width: 100%;
  z-index: 1000;
  padding: 40px 0 80px;
  font-size: 16px;
  position: sticky;
  top: 0;
}

.header__nav-wrapper {
  gap: 12px;
  display: flex;
}

.header__nav-wrapper .button__nav {
  -webkit-backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  background-color: #fffc;
  border: 1.3px solid #0f1b5e1a;
  border-radius: 8px;
  font-weight: 500;
}

.header__nav-wrapper .button__nav--link {
  color: #101010;
  padding: 6px 12px;
  text-decoration: none;
  display: block;
}

.header__nav-wrapper .button__nav--dark {
  color: #fff;
  background-color: #1e1e1e;
  border: 1.3px solid #ffffff1a;
  font-weight: 400;
}

.header__nav-wrapper .button__nav--dark .button__nav--link {
  color: #fff;
}

.hero {
  padding-bottom: 80px;
}

.hero .hero__wrapper {
  flex-direction: column;
  gap: 16px;
  padding-bottom: 80px;
  display: flex;
}

.hero .hero__wrapper .case-title {
  margin-bottom: 16px;
  line-height: 140%;
}

@media (width <= 680px) {
  .contener {
    height: 90%;
  }
}

.toolbar {
  width: 100%;
  height: 19px;
  background: linear-gradient(#f4f4f4, #e7e7e7);
  position: relative;
}

.toolbar__dots {
  gap: 4px;
  display: flex;
  position: absolute;
  top: 6px;
  left: 8px;
}

.toolbar__dots .toolbar__dot {
  width: 8px;
  height: 8px;
  background-color: #cbcbcd;
  border-radius: 100px;
}

.toolbar__title {
  color: #00000080;
  font-size: 9px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dark .toolbar {
  background: #343434;
}

.dark .toolbar__dot {
  background-color: #686866;
}

.dark .toolbar__title {
  color: #ffffff80;
}

.desktop {
  border-radius: 4px;
  flex-direction: column;
  gap: 0;
  display: flex;
  overflow: hidden;
}

.desktop img {
  max-width: 680px;
  width: 100%;
}

.contact {
  padding: 80px 0;
}

.contact .contact__links {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.contact .contact__links .cs-link {
  color: #19a0fc;
  display: block;
}

.contact .contact__links .cs-link:after {
  content: " " url("arrow-up-right.75d75e4c.svg");
}

